<template>
  <el-menu :default-active="activeIndex" class="el-menu" mode="horizontal" @select="handleSelect">
    <el-menu-item>
      <img src="./img/logo.png" alt="music" class="logo">
    </el-menu-item>
    <el-menu-item index="discover"></el-menu-item>
    <el-menu-item index="recommend"></el-menu-item>
    <el-menu-item index="latestSongs"></el-menu-item>
    <el-menu-item index="latestMVs">MV</el-menu-item>
    <el-menu-item class="search">
      <el-input placeholder="//MV/" v-model="search" class="input-with-select" @keydown.enter.native="searchByName">
        <el-button @click="searchByName" slot="append" type="mini" icon="el-icon-search"></el-button>
      </el-input>
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      activeIndex: 'discover',
      search: ''
    }
  },
  watch: {
    '$route.path': function (val) {
      val = val.slice(1)
      console.log(val)
      this.activeIndex = val
    },
    '$route.params.songName': function (val) {
      this.search = val
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
      if (key) {
        this.$router.push('/' + key)
      }
    },
    searchByName () {
      console.log(this.search)
      if (this.search === '' || this.search == null) {
        this.$message({
          message: '//MV/',
          type: 'error'
        })
        return false
      }
      this.$router.push('/search/' + this.search)
    }
  }
}
</script>

<style scoped lang="less">
.logo{
  height: 60px;
}
.el-menu-item:first-child{
  border-bottom: none!important;
}
.el-menu-item:last-child{
  border-bottom: none!important;
}
.search{
  float: right!important;
}
</style>
